.header {
    text-align: center;
    padding: 5px;
    margin-bottom: 17px;
    /* margin-left: 35px; */
    /* margin-right: 35px; */
    font-family: Ozxgen, sans-serif;
    font-size: 22px;
    border-bottom: 2px solid #70a1ff;
}

.box-container {
    display: flex;
    flex-direction: column;
    height: 100%;
    max-width: 24em;
    min-width: 21em;
    justify-content: center;
    align-items: center;    
    flex-direction: column;
    margin-top: 10%;
    margin-left: auto;
    margin-right: auto; 

    box-shadow: 0px 0px 20px 0px rgba(15, 15, 15, 0.2);
    border-radius: 6px;
    padding: 25px;
}


.inner-container {
    transition: visibility 0.2s ease-out;
    width: 100%;
}

.input-group {
    display: flex;
    flex-direction: column;
    margin-top: 5px;
    margin-bottom: 5px;
}


.signup-input {
    height: 1.8em;
    border-radius: 3px;
    border: 1px solid #70a1ff;
    transition: border 0.4s ease-out;
    padding: 13px;
    font-family: Oxygen, sans-serif;
    font-size: 19px;
    color: #3e3e42;
    background: rgba(15, 15, 15, 0.01);
}

.signup-input:hover {
    border: 1px solid #70a1ff;
}

.signup-label {    
    font-family: Oxygen, sans-serif;
    font-size: 18px;
    font-weight: 400;
    padding: 10px;
}

.signup-btn {
    padding: 2px 30px;
    /* background-color: #417cef; */
    border: 0;
    height: 2.5em;
    width: 100%;
    font-size: 18px;
    border-radius: 3px;
    font-family: Oxygen, sans-serif;
    background-color: rgba(15, 15, 15, 0.03);
    margin-top: 30px;
    border: 2px solid #417cef;
    transition: background-color 0.3s ease-out;
    cursor: pointer;
}

.box {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.signup-btn:hover,
.signup-btn:focus {
    background-color: #417cef;
    color: white;
}

.signup-err {
    color: #e74c3c;
    font-size: 16px;
}
